<!-- 日记 -->
<template>
	<view class="container">
		<u-navbar :is-back="false" title="全部日记" :background="background" title-color="#fff">
			<view class="navbar_icon">
				<button class="btn_click" @click="goToWriteDiary"></button>
			</view>
		</u-navbar>
		<view class="content">
			<view v-for="(item, index) in data_list" :index="index" :key="index">
				<view class="d_con_item" v-show="item.show">
					<view class="d_con_data">
						<view class="num">{{item.num}}</view>
						<view class="day">{{item.day}}</view>
						<view class="yymm">{{item.yymm}}</view>
					</view>
					<view class="d_con_content">
						<view class="d_content_view">
							<view class="txt">{{item.content}}</view>
							<view class="time">{{item.time}}</view>
						</view>
					</view>
				</view>
			</view>
			<view style="text-align: center; margin-top: 30rpx;">上拉加载更多</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				background: {
					backgroundColor: '#57B883'
				},
				data_list:[
					{
						content:'今天去做了个兼职',
						time:'17:59',
						num:'15',
						day:'周四',
						yymm:'2020.10',
						show:true
					},
					{
						content:'今天去爬山了',
						time:'19:59',
						num:'18',
						day:'周日',
						yymm:'2020.10',
						show:true
					},
					{
						content:'今天去逛了商场',
						time:'15:39',
						num:'21',
						day:'周三',
						yymm:'2020.10',
						show:true
					},
					{
						content:'今天老师布置了期末大作业',
						time:'11:30',
						num:'22',
						day:'周四',
						yymm:'2020.10',
						show:true
					},
					{
						content:'',
						time:'11:50',
						num:'23',
						day:'周五',
						yymm:'2020.10',
						show:false
					}
				]
			}
		},
		onPullDownRefresh() {
			console.log('refresh');
			setTimeout(function() {
				uni.stopPullDownRefresh();
			}, 1000);
		},
		methods: {
			goToWriteDiary(){
				uni.navigateTo({
					url:'../writediary/writediary'
				})
			}
		},
		onLoad: function(){
			let content = uni.getStorageSync('content');
			if(content){
				this.data_list[4].content = content
				this.data_list[4].show = true
			}else{
				this.data_list[4].content = ""
				this.data_list[4].show = false
			}
		}
	}
</script>

<style>
	.navbar_icon{
		position: relative;
	}
	.navbar_icon::after{
		content: "";
		width: 50rpx;
		height: 50rpx;
		background: url(../../static/images/data.png) no-repeat;
		position: absolute;
		left: 40rpx;
		bottom: -30rpx;
	}
	.navbar_icon::before{
		content: "";
		width: 50rpx;
		height: 50rpx;
		background: url(../../static/images/diary.png) no-repeat;
		position: absolute;
		left: 670rpx;
		bottom: -30rpx;
	}
	.btn_click{
		width: 50rpx;
		height: 50rpx;
		opacity: 0;
		position: absolute;
		left: 670rpx;
		bottom: -30rpx;
	}
	.content{
		height: 1100rpx;
		background-color: #F1F1F1;
		width: 100%;
	}
	.content .d_con_item{
		width: 100%;
		display: flex;
		height: 190rpx;
	}
	.content .d_con_item .d_con_data{
		border-right: 1rpx solid #ccc;
		padding-top: 30rpx;
		padding-left: 20rpx;
		padding-right: 30rpx;
		width: 20%;
		display: flex;
		flex-wrap: wrap;
		position: relative;
	}
	.content .d_con_item .d_con_data::after{
		content: "";
		width: 30rpx;
		height: 30rpx;
		position: absolute;
		display: block;
		border: 1rpx solid #ccc;
		background-color: #f4f4f5;
		border-radius: 50%;
		left: 133rpx;
		top: 50rpx;
	}
	.content .d_con_item .d_con_data .num{
		font-size: 35rpx;
	}
	.content .d_con_item .d_con_data .day{
		font-size: 23rpx;
		margin-top: 10rpx;
		margin-left: 10rpx;
	}
	.content .d_con_item .d_con_data .yymm{
		font-size: 21rpx;
		margin-bottom: 100rpx;
	}
	.content .d_con_item .d_con_content{
		width: 80%;
		padding: 30rpx 20rpx 10rpx 50rpx;
	}
	.content .d_con_item .d_con_content .d_content_view{
		background-color: #fff;
		width: 100%;
		height: 150rpx;
		border-radius: 10rpx;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		padding: 10rpx;
		padding-left: 20rpx;
		position: relative;
	}
	.content .d_con_item .d_con_content .d_content_view::after{
		content: "";
		position: absolute;
		width:0;
		height:0;
		border-width:15rpx 20rpx;
		border-style:solid;
		border-color:transparent #fff transparent transparent;
		left:-36rpx;
		top:24rpx;
	}
	.content .d_con_item .d_con_content .d_content_view .txt{
		flex-wrap: wrap;
	}
	.content .d_con_item .d_con_content .d_content_view .time{
		font-size: 20rpx;
		color: #999999;
	}
</style>
